import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown';
import axios from 'axios';



class Tutorial extends Component{

    constructor(props){
        super(props);
        this.state = {
            input : '# This is a header\n\nAnd this is a paragraph'
        }
    }

    componentDidMount() {
        axios.get('http://sample-env-2.8t95hzwxpb.us-east-1.elasticbeanstalk.com/math')
        .then( (response) => {
            console.log(response.data[0].statement );
            this.setState({
                input: response.data[0].statement
            })

        })
         .catch( (error) => {

        });
    }


    render(){

        return (
            <ReactMarkdown source={'*dd*'} />
        );
    }
}

export default Tutorial;


